import React, { Component } from 'react';
import { Container, Button } from 'reactstrap';
class Factura extends Component {
  render() {
    return (
      <Container>
        <h1> Ganador {this.props.vendedor}!</h1>
        <Button className='float-right' onClick={this.props.onRestart}>Reiniciar</Button>
        <div className='container'>
          <div className='card'>
            <div className='card-header'>
              Invoice:
              <strong> {this.props.facturaDate}</strong>
              <span className='float-right'>
                {' '}
                <strong>Status:</strong> Pending {this.props.facturaId}
              </span>
            </div>
            <div className='card-body'>
              <div className='row mb-4'>
                <div className='col-sm-6'>
                  <h6 className='mb-3'>From:</h6>
                  <div>
                    <strong>Alegra</strong>
                  </div>
                  <div>Madalinskiego 8</div>
                  <div>71-101 Szczecin, Poland</div>
                  <div>Email: info@info.info</div>
                  <div>Phone: +48 444 666 3333</div>
                </div>

                <div className='col-sm-6'>
                  <h6 className='mb-3'>To:</h6>
                  <div>
                    <strong>{this.props.cliente}</strong>
                  </div>
                  <div>Attn: Yesid Gonzalez</div>
                  <div>43-190 Mikolow, Poland</div>
                  <div>Email: marek@daniel.com</div>
                  <div>Phone: +48 123 456 789</div>
                </div>
              </div>

              <div className='table-responsive-sm'>
                <table className='table table-striped'>
                  <thead>
                    <tr>
                      <th className='center'>#</th>
                      <th>Item</th>
                      <th>Description</th>

                      <th className='center'>Qty</th>
                      <th className='right'>Total</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td className='center'>1</td>
                      <td className='left strong'>{this.props.concursoName}</td>
                      <td className='left'>{this.props.concursoDescription}</td>

                      <td className='right'>{this.props.total}</td>
                      <td className='right'>${this.props.totalPago}</td>
                    </tr>
                  </tbody>
                </table>
              </div>
              <div className='row'>
                <div className='col-lg-4 col-sm-5'></div>

                <div className='col-lg-4 col-sm-5 ml-auto'>
                  <table className='table table-clear'>
                    <tbody>
                      <tr>
                        <td className='left'>
                          <strong>Subtotal</strong>
                        </td>
                        <td className='right'>${this.props.totalPago}</td>
                      </tr>
                      <tr>
                        <td className='left'>
                          <strong>Total</strong>
                        </td>
                        <td className='right'>
                          <strong>${this.props.totalPago}</strong>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
      </Container>
    );
  }
}

export default Factura;
