import React, { Component } from 'react';
import {
  Container,
  Button,
  InputGroup,
  InputGroupAddon,
  Input,
  Row,
  Col,
  ListGroup,
  ListGroupItem,
  Badge
} from 'reactstrap';

import Gallery from '../components/gallery';
import Factura from '../components/factura';

const fetch = require('node-fetch');

export default class home extends Component {
  form = {};
  constructor(props) {
    super(props);
    this.state = this.getInitialState();
    this.handleRestart = this.handleRestart.bind(this);
  }

  getInitialState = () => {
    const initialState = {
      items: 0,
      search: '',
      vendedores: [],
      images: [],
      votos: 0,
      ganadores: {},
      winner: false,
      vendedor: '',
      cliente: '',
      vendedorId: '',
      concursoName: '',
      concursoDescription: '',
      facturaId: 0,
      facturaDate: '',
      totalPago: 0
    };
    return initialState;
  };

  componentDidMount() {
    console.log('mount');
    const fetchItems = async () => {
      const fetchItem = await fetch('https://api.alegra.com/api/v1/sellers/', {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          Authorization:
            'Basic eWVzaWQuZGV2OTNAZ21haWwuY29tOjgyOWFmMTg4ZWU1N2M3YWUyZjc4',
          Accept: 'application/json'
        }
      });
      const item = await fetchItem.json();
      await this.setState({
        vendedores: item,
        items: 9
      });
    };
    fetchItems();
  }

  handleRestart = async () => {
    console.log(this.getInitialState());
    
    await this.setState(this.getInitialState());
    console.log('mount');
    console.log(this.state)
    const fetchItems = async () => {
      const fetchItem = await fetch('https://api.alegra.com/api/v1/sellers/', {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          Authorization:
            'Basic eWVzaWQuZGV2OTNAZ21haWwuY29tOjgyOWFmMTg4ZWU1N2M3YWUyZjc4',
          Accept: 'application/json'
        }
      });
      const item = await fetchItem.json();
      await this.setState({
        vendedores: item,
        items: 9
      });
    };
    fetchItems();
  };

  handleInputChange = async e => {
    this.form[e.target.name] = e.target;
    await this.setState({
      [e.target.name]: e.target.value
    });
  };

  handleButton = async e => {
    if (this.state[e] === undefined) {
      await this.setState(prevState => ({
        [e]: 3,
        ganadores: {
          ...prevState.ganadores,
          [e]: 3
        }
      }));
    } else {
      await this.setState(prevState => ({
        [e]: 3 + this.state[e],
        ganadores: {
          ...prevState.ganadores,
          [e]: 3 + this.state[e]
        }
      }));
    }
    console.log(this.state.ganadores);
    await this.setState({
      votos: this.state.votos + 3,
      images: []
    });
    if (this.state[e] >= 20) {
      const fetchWinner = async () => {
        const fetchItem = await fetch(
          `https://api.alegra.com/api/v1/sellers/${e}`,
          {
            method: 'GET',
            headers: {
              'Content-Type': 'application/json',
              Authorization:
                'Basic eWVzaWQuZGV2OTNAZ21haWwuY29tOjgyOWFmMTg4ZWU1N2M3YWUyZjc4',
              Accept: 'application/json'
            }
          }
        );
        const item = await fetchItem.json();
        await this.setState({
          winner: true
        });
        console.log(item.name, 'wins!');
        const data = {
          date: '2019-10-29',
          dueDate: '2019-11-11',
          client: 1,
          seller: e,
          items: [
            {
              id: 1,
              price: 120,
              quantity: this.state.items,
              description: 'Ganador Concurso Vendedores Â¡a correr!'
            }
          ],
          stamp: {
            generateStamp: true
          }
        };
        const fetchPostFactura = await fetch(
          `https://api.alegra.com/api/v1/invoices`,
          {
            method: 'POST',
            body: JSON.stringify(data),
            headers: {
              'Content-Type': 'application/json',
              Authorization:
                'Basic eWVzaWQuZGV2OTNAZ21haWwuY29tOjgyOWFmMTg4ZWU1N2M3YWUyZjc4',
              Accept: 'application/json'
            }
          }
        );
        const factura = await fetchPostFactura.json();
        console.log(factura);
        await this.setState({
          facturaId: factura.invoice.id,
          facturaDate: factura.invoice.date,
          vendedor: factura.invoice.seller.name,
          cliente: factura.invoice.client.name,
          vendedorId: factura.invoice.seller.id,
          concursoName: factura.invoice.items[0].name,
          concursoDescription: factura.invoice.items[0].description,
          totalPago: factura.invoice.items[0].total
        });
      };
      fetchWinner();
    }
  };

  handleMytest = () => {
    if (this.state.search && this.form['search'].value.length > 0) {
      const fetchapi = async () => {
        await window.gapi.client.setApiKey(
          'AIzaSyDazJWiT0pjLAghEGMtjeTegz7qQXC6d4U'
        );
      };
      fetchapi();

      const fetchGapi = async () => {
        console.log(this.state.search);
        window.gapi.client
          .load(
            'https://content.googleapis.com/discovery/v1/apis/customsearch/v1/rest'
          )
          .then(
            () => {
              console.log('GAPI client loaded for API');
              window.gapi.client.search.cse
                .list({
                  q: this.state.search,
                  cx: '005215205933549556099:si9ryhbwt9j',
                  num: this.state.items,
                  searchType: 'image'
                })
                .then(
                  response => {
                    // Handle the results here (response.result has the parsed body).
                    this.setState({
                      images: response.result.items
                    });
                    console.log('Response', this.state.images);
                  },
                  function(err) {
                    console.error('Execute error', err);
                  }
                );
            },
            function(err) {
              console.error('Error loading GAPI client for API', err);
            }
          );
      };
      fetchGapi();

      this.form['search'].value = '';
    }
  };

  render() {
    const isWinner = this.state.winner;
    return (
      <Container className='mt-4'>
        <InputGroup>
          <Input
            placeholder='Ingresa una busqueda para encontrar imagenes'
            name='search'
            onChange={this.handleInputChange}
            noValidate
          />
          <InputGroupAddon addonType='append'>
            <Button className='button' onClick={this.handleMytest}>
              Buscar
            </Button>
          </InputGroupAddon>
        </InputGroup>
        <p className={this.getClassNone()}>{this.formatSearch()}</p>

        {isWinner ? (
          <Row>
            <Factura
              onRestart={this.handleRestart}
              total={this.state.votos}
              vendedor={this.state.vendedor}
              cliente={this.state.vendedor}
              vendedorId={this.state.vendedorId}
              concursoName={this.state.concursoName}
              concursoDescription={this.state.concursoDescription}
              totalPago={this.state.totalPago}
              facturaId={this.state.facturaId}
              facturaDate={this.state.facturaDate}
            />
          </Row>
        ) : (
          <Container className={this.getClassNone()}>
            <h1 className='text-center'>Gallery</h1>
            <Row>
              {this.state.images.map((item, i) => (
                <Col key={i} sm='12' md='6'>
                  <Gallery
                    vendorId={this.state.vendedores[i].id}
                    link={item.link}
                    vendorName={this.state.vendedores[i].name}
                    totalVotes={this.state[this.state.vendedores[i].id]}
                    titulo={item.title}
                    onClickPress={this.handleButton}
                  />
                </Col>
              ))}
            </Row>
          </Container>
        )}
        <ListGroup className={this.getClassListNone()}>
          {Object.keys(this.state.ganadores).map(id => (
            <ListGroupItem key={id} className='justify-content-between'>
              Vendedor {this.state.vendedores[id - 1].name}{' '}
              <Badge color='dark' pill>
                {this.state.ganadores[id]}
              </Badge>
            </ListGroupItem>
          ))}
          <ListGroupItem>
            Total votos:{' '}
            <Badge className='button' pill>
              {this.state.votos}
            </Badge>{' '}
          </ListGroupItem>
        </ListGroup>
      </Container>
    );
  }
  getClassNone() {
    let classes = 'm-2';
    classes += this.state.images.length === 0 ? ' empty' : '';
    return classes;
  }
  getClassListNone() {
    let classes = 'm-2';
    classes += this.state.images.length > 0 ? ' empty' : '';
    return classes;
  }

  formatSearch() {
    const { items } = this.state;
    return items === 0 ? '' : `Numero de concursantes: ${items}`;
  }
}
