import React, { Component } from 'react';
import {
  Container,
  CardGroup,
  Card,
  CardImg,
  CardText,
  CardTitle,
  CardBody,
  Button
} from 'reactstrap';

export class Gallery extends Component {
  render() {
    return (
      <Container className='mt-4'>
        <CardGroup className={this.getClassNone()}>
          <Card>
            <CardBody>
              <CardImg
                top
                width='100%'
                src={this.props.link}
                alt='imagensita'
              />
              <CardTitle className='text-center'>
                <p>Vendedor: <strong>{this.props.vendorName}</strong> </p>
                <hr/>
              </CardTitle>
              <CardText
                style={{ height: "5rem", overflow: 'auto', textOverflow: 'ellipsis' }}
              >
                {' '}
                Votos totales: {this.props.totalVotes}
                {this.props.totalVotes === undefined && 0} <br />
                {this.props.titulo}
              </CardText>
              <div className={this.getButtonC(this.props.vendorId)}>
                <Button
                  className='button'
                  size='lg'
                  onClick={() => this.props.onClickPress(this.props.vendorId)}
                  block
                >
                  Vota!
                </Button>
              </div>
            </CardBody>
          </Card>
        </CardGroup>
      </Container>
    );
  }
  getButtonC(e) {
    let classes = 'text-center';
    classes += this.props.totalVotes > 20 ? ' empty' : '';
    return classes;
  }

  getClassNone() {
    let classes = 'm-2';
    classes += this.props.link === undefined ? ' empty' : '';
    return classes;
  }
}

export default Gallery;
